  }
  :root {
    --color-component-advanced: #F51767;
    --color-component-primary: #000;
    --color-component-secondary: #ffff;
    --color-component-advanced-hover: #f8176ab7;
  }
  .skills_h1 {
    color: var(--color-component-advanced);
    text-transform: uppercase;
    text-align: center;
    font-family: CodeNext-Font-Family-UI-prototype-component-extra-bold_mvc;
    font-size: calc(20px + 3vmin);
    transition: all 0.5s ease-out;
    margin-bottom: 10px;
  }
.skills_h1 span {
  transform: scale(0.8);
  transition: all 0.5s ease-out;
}
.skills_h1 span:hover {
  transform: scale(1);
  transition: all 0.5s ease-out;
}